<template>
  <div class="MyHonourTotalBackground">
    <!--     <input
            type="text"
            class="MyHonourUserNumberInput"
            v-model="MyHonourUserNumber"
            placeholder="请输入学生学号"
          />
          <button  class="MyHonourSeekButton" @click="MyHonourSeekButtonClick">查询</button>
          <button  class="MyHonourEditButton" @click="MyHonourEditButtonClick">编辑</button>
       -->
    <div class="MyHonourTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 3%">姓名</th>
            <th style="width: 4%">荣誉证书名</th>
            <th style="width: 4%">荣誉证书编号</th>
            <th style="width: 4%">获得时间</th>
            <th style="width: 4%">颁发部门</th>
            <th style="width: 4%">获奖级别</th>
            <!-- 校级，院级，国家级 -->
            <th style="width: 2%">参加形式</th>
            <th style="width: 2%">排名</th>
            <th style="width: 5%">照片</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(studentRecord, index) in MyHonourTable" :key="index">
            <td>
              {{ studentRecord.studentAwardDetails[0].studentName }}
            </td>
            <td>
              {{ studentRecord.studentAwardDetails[0].awardName }}
            </td>
            <td>
              {{ studentRecord.studentAwardDetails[0].certificateNumber }}
            </td>

            <td>
              {{ formatDate(studentRecord.studentAwardDetails[0].getTime) }}
            </td>
            <td>
              {{ studentRecord.studentAwardDetails[0].issuingDepartment }}
            </td>
            <td>
              {{ studentRecord.studentAwardDetails[0].classification }}
            </td>
            <td>
              {{ studentRecord.studentAwardDetails[0].participateForm }}
            </td>

            <td>{{ studentRecord.studentAwardDetails[0].ranks }}</td>
            <td
              @click="
                pictureInspectClick(studentRecord.studentAwardDetails[0].photo)
              "
            >
              <img
                :src="studentRecord.studentAwardDetails[0].photo"
                alt="获奖照片"
                width="100"
                height="30"
              />
            </td>
            <!-- 查看详情 -->
            <td @click="ViewDetails(studentRecord)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="MyHonourPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>

    <div v-if="ifDetails" class="MyHonourDetailsBackground">
      <div class="MyHonourDetailsFormBackground">
        <span class="MyHonourDetailsFormTitle">学生荣誉详细信息</span>
        <form class="MyHonourDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div class="label-row">
            <label>院系 : {{ studentDetails[0].college }}</label>
          </div>
          <div class="label-row">
            <label>专业 : {{ studentDetails[0].major }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>荣誉证书名 : {{ data.awardName }}</label>
            </div>
            <div class="label-row">
              <label>荣誉证书编号 : {{ data.certificateNumber }}</label>
            </div>
            <div class="label-row">
              <label>获得时间 : {{ data.getTime }}</label>
            </div>
            <div class="label-row">
              <label>颁发部门 : {{ data.issuingDepartment }}</label>
            </div>
            <div class="label-row">
              <label>荣誉等级 : {{ data.classification }}</label>
            </div>
            <div class="label-row">
              <label>参加形式 : {{ data.participateForm }}</label>
              <label>排名 : {{ data.ranks }}</label>
            </div>
            <div class="label-row">
              <label>照片 : </label>
              <label
                ><img :src="data.photo" alt="图片描述" width="400" height="300"
              /></label>
            </div>
            <br />
          </div>
        </form>
        <button
          class="MyHonourDetailsFormCloseButton"
          @click="MyHonourDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>

    <div
      v-if="ifPicture"
      class="MyHonourDetailsBackground"
      @click="MyHonourDetailsBackgroundClick"
    >
      <img :src="this.pictureURL" alt="图片描述" width="70%" height="300" />
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      MyHonourTable: [],
      ifDetails: false,
      ifPicture: false,
      pictureURL: "",
      studentDetails: {},
      currentPage: 1,
      pageSize: 13,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    formatDate(date) {
      const d = new Date(date);
      return `${d.getFullYear()}-${(d.getMonth() + 1)
        .toString()
        .padStart(2, "0")}-${d.getDate().toString().padStart(2, "0")}`;
    },
    MyHonourDetailsBackgroundClick() {
      this.ifPicture = false;
    },
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    pictureInspectClick(picture) {
      this.pictureURL = picture;
      this.ifPicture = true;
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getAcademicCompetition",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.MyHonourTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },

    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },

    RenewLabel(studentRecord) {
      this.studentDetails = studentRecord.studentAwardDetails;
    },
    MyHonourDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.MyHonourPagination {
  position: absolute;
  top: 95%;
}
.MyHonourUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.MyHonourTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.MyHonourTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/MyHonourForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
